
import { defineComponent } from 'vue';
import { useRoute } from 'vue-router';

import clubs from '~data/clubs';

export default defineComponent({
  name: 'ClubPage',
  setup() {
    const route = useRoute();

    const clubSlug = route.params.clubSlug.toString();
    if (clubs[clubSlug] === undefined) {
      return {
        found: false,
      };
    }

    const {
      name,
      shortDescription,
      longDescription,
      prerequisites,
      meetingTimes,
      meetingPlatform,
      moreInfo,
    } = clubs[clubSlug].data;

    return {
      found: true,

      name,
      shortDescription,
      longDescription,
      prerequisites,
      meetingTimes,
      meetingPlatform,
      moreInfo,
    };
  },
});
